<template>
    <div class="dateinput">
    <v-layout >
      <v-flex xs12 sm6 md4>
          <template v-slot:activator="{ on }">
            <v-text-field
              v-model="selecteddate"
              label="Select date"
              prepend-icon="event"
              readonly
              disabled=""
            ></v-text-field>
          </template>
          <v-date-picker readonly v-model="selecteddate" disabled >
          </v-date-picker>
      </v-flex>
    </v-layout>
    </div>
</template>
<script>
export default {
    props:['questiondata'],
    data() {
        return {
            selecteddate: new Date().toISOString().substr(0, 10),
            menu: false
        }
    },
    methods :{
    },
    watch:{
    },
    created: function () {
    var this_attempt = window.surveyattemptid
    axios.post(`/getattemptanswer`,{
      'questionid':this.questiondata._id,
      'attemptid':this_attempt._id
      }).then(response => {
        if(response.data.status == 1){
          if(response.data.data != null){
            this.selecteddate = JSON.parse(response.data.data.answer)
          }
        }
    })
    .catch(e => {
      this.errors.push(e)
    })
  }

}
</script>



