<template>
    <div :style="styles" :id="id"> </div>
</template>

<script>
    import Highcharts from 'highcharts'
    import Highmaps from 'highcharts/highmaps'
    import Hightstock from 'highcharts/highstock'

    export default {
        props: {
            options: {
                type: Object,
                required: true
            },
            type: {
                type: String,
                default: 'highcharts'
            },
            width: {
                type: String,
                default: '100%'
            },
            height: {
                type: String,
                default: '250px'
            },
            series: {
                type: Array,
                default: []
            }
        },

        data() {
            return {
                id: 'highcharts-' + new Date().getTime(),
                chart: null
            }
        },

        watch: {
            options() {
                this.build();
            },
            width() {
                this.build();
            },
            height() {
                this.build();
            },
            series(val) {
                this.setSeries(val);
            }
        },

        computed: {
            styles() {
                return {
                    width: isNaN(this.width) ? this.width : this.width + '%',
                    height: isNaN(this.height) ? this.height : this.height + '%'
                }
            }
        },

        methods: {
            build() {
                switch (this.type) {
                    case 'stock':
                    case 'highstock':
                        this.chart = Hightstock.chart(this.id, this.options);
                        break;
                    case 'maps':
                    case 'highmaps':
                        this.chart = Highmaps.chart(this.id, this.options);
                        break;

                    default:
                        this.chart = Highcharts.chart(this.id, this.options);
                        break;
                }
            },
            setSeries(val) {
                this.options = Object.assign({}, this.options, { series: val });
            }
        },
        mounted() {
            this.build();
        }
        // created() {
        //     this.build();
        // },
    }

</script>


