<template>
    <div class="dateinput">
    <v-layout >
      <v-flex xs12 sm6 md4>
          <template v-slot:activator="{ on }">
            <v-text-field
              v-model="date"
              label="Select date"
              prepend-icon="event"
              readonly
              v-on="on"
            ></v-text-field>
          </template>
          <v-date-picker v-model="date" >
          </v-date-picker>
      </v-flex>
    </v-layout>
    </div>
</template>
<script>
export default {
    props:['maskformat'],
    data() {
        return {
            date: new Date().toISOString().substr(0, 10),
            menu: false
        }
    }
}
</script>



