<template>
     <transition name="modal">
    <div class="modal-mask"> 
      <div class="modal-wrapper">
         <div class="modal-container">
          <div class="modal-header">
            <v-text-field v-model="urlAddress" label="Enter URL" ></v-text-field>
          </div>
          <div class="modal-footer">
              <button class="exportbutton" @click="insertUrl(urlAddress)">OK</button>
              <button class="exportbutton" @click="closeModel()">Cancel</button>
          </div>
        </div>
        </div>
    </div>
  </transition>
</template>

<script>
export default {
  data() {
      return {
          urlAddress: ""
      }
  },
    methods: {
        insertUrl(data) {
            this.$emit('urlAddress', data)  
        },
        closeModel() {
            this.$emit('closeModel')  
        }
    }
}
</script>

<style scoped>
.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  display: table;
  transition: opacity .3s ease;
}

.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.modal-container {
  width: 300px;
  margin: 0px auto;
  padding: 20px 30px;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
  transition: all .3s ease;
  font-family: Helvetica, Arial, sans-serif;
}

.modal-enter .modal-container,
.modal-leave-active .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}
</style>


